@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Great+Vibes&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Great+Vibes&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  font-family: 'Poppins', sans-serif;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}


.navbar{
    background: #40b0bd;
    font-size: large;
}

.nav-link{
    border-bottom: solid 1px #40b0bd;
}

.nav-link:hover{
    border-bottom: solid 1px #0e0d0d;
    color: rgb(255, 255, 255);
}
.jumbotron {
  background: #40b0bd !important;
}

.cloud-container {
  max-width: 100;
  min-height: 50%;
  margin: 0;
  padding: 0;
  overflow: hidden;
  background-color: #40b0bd;
  /* background: linear-gradient(to bottom, #40b0bd 1%, #40b0bd 100%); */
}

.cloud-container {
  background: url(/portfolio/static/media/clouds.9c9bd8d6.png);
  background-size: contain;
  background-repeat: repeat;
  background-color: #41b0bf;
  position: relative;
}

.cloud-container .name {
  font-family: "Great Vibes", sans-serif;
  font-weight: 400;
  font-size: 80px;
  color: #e8f1ed;
}

.cloud-container .sub-name {
  font-family: "Poppins", sans-serif;
  color: #e8f1ed;
  font-size: xx-large;
}

/* .btn{
  font-family: "Poppins", sans-serif;
  /* width: 50%; */
  /* padding: 15px; */
/* } */

.cloud-container .outlined-white {
  font-family: "Poppins", sans-serif;
  background-color: #41b0bf;
  color: #fff;
  border-radius: 30px;
  padding: 15px 25px;
  border: 1px solid #fff;
  font-size: 14px;
  letter-spacing: 1px;
}

.jumbotron .scroll-link{
  font-family: "Poppins", sans-serif;
  background-color: #41b0bf;
  color: #fff;
  text-decoration: none;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}

.jumbotron .scroll-link:hover{
  background: #fff;
  color: #41b0bf;
  border: none;
}

.cloud-container .outlined-white:hover {
  background: #fff;
  color: #41b0bf;
}

@media (max-width: 450px) {
  .container{
    width: 100%;  
  }

  .jumbotron {
    width: 100%;
  }
  /* .cloud-container {
    width: 450px;
  } */

  #cloud-intro {
    background: none;
  }

  .cloud-container .name {
    font-family: "Great Vibes", sans-serif;
    font-weight: 400;
    font-size: 80px;
    color: #e8f1ed;
    display: contents;
  }

  .cloud-container .sub-name {
    font-family: "Poppins", sans-serif;
    color: #e8f1ed;
    font-size: xx-large;
  }
}

.figure{
    display: -webkit-flex !important;
    display: flex !important;
    padding-left: 96px;
    padding-right: 96px;
    padding-top: 30px;
    background: #f0f8ff;
    margin: 0;
}

.profile-pic{
    border-radius: 50% !important;
    margin: 10px;
    width:250px !important;
    height:200px !important;
}

.figure .figure-caption{
    font-family: 'Poppins', sans-serif;
    font-size: 20px;
    color: #838383;
}

i {
    font-size: 40px;
    color: #3b5999;
    padding: 15px;
}

.fa-envelope{
    color: #3b5999;  
}

.fa-linkedin{
    color: #0077B5;
}

.fa-github{
    color: rgb(179, 24, 96);
}

.fa-instagram{
    color: #e4405f;
}

.fa-whatsapp{
    color: #25D366;
}

@media (max-width: 450px) {
    .figure {
        display: block !important;
        padding-right: 0%;
        padding-left: 0%;
    }

    .profile-pic{
        border-radius: 50% !important;
        margin: 10px;
        width:200px !important;
        height:200px !important;
    }

    .social-links{
        
        display: inline-block;
    }
  }
#skills{
    background-color: antiquewhite;
    padding-bottom: 3%;
}

.title{
    font-size: 35px;
    font-weight: 500;
    font-family: 'Poppins', sans-serif;
    color: #1f9ac0;
    margin: 0;
}

.skills{
    display: -webkit-flex;
    display: flex;
    font-family: 'Poppins', sans-serif;
    -webkit-align-items: center;
            align-items: center;
    width: 100%;
    padding: 0% !important;
}

.skills span{
    margin: auto;
}

.skills p{
    font-family: 'Poppins', sans-serif;
    font-size: 23px;
    color: #68c01f;
    margin: auto;
}

.container img{
    padding: 10px 15px 15px 15px;
}

.container .fa-html5{
    color: #1f4ac0;
}

.container .fa-css3-alt{
    color: #1f4ac0bd;
}

.container .fa-js{
    color: #9acd32;
}

.container .fa-java{
    color: #ff0000;
}

.container .fa-react{
    color: rgba(0, 0, 0, 0.774);
}

@media (max-width: 450px) {
    .skills{
        display: block;
    }
}
.contact {
  padding: 3% 4% 0 4%;
}

.contact .head-text {
  font-family: "Poppins", sans-serif;
  font-size: 300%;
  color: #1105059f;
  font-weight: 500;
  margin: 0;
}

.contact .sub-text {
  font-family: "Poppins", sans-serif;
  font-size: 150%;
  color: #dd1515;
  margin: 0;
}

/* .container .fab{
    font-size: 50px;
} */

.container .sign {
  font-family: "Great Vibes", sans-serif;
  font-size: 40px;
  color: #41b0bf !important;
}

.container .fa-heart {
  font-size: 120%;
  padding: 1%;
  color: #dd1515;
}

@media (max-width: 450px) {
  .contact .row {
    display: block;
  }

  .contact .container{
      padding: 0 !important;
  }
}

